.about {
  @media screen and (min-width: 320px) {
    margin-top: 39px;
  }

  @media screen and (min-width: 768px) {
    margin-top: 50px;
  }

  @media screen and (min-width: 1366px) {
    margin-top: 153px;
  }
}

.about__title {
  font-size: 20px;
  line-height: 1.3;
  font-weight: 800px;
  color: var(--primary-text-color);

  @media screen and (min-width: 768px) {
    font-size: 26px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 46px;
    margin-bottom: 32px;
    text-align: start;
  }
}

.about__box {
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    display: flex;
    margin-top: 30px;
  }

  @media screen and (min-width: 1366px) {
    margin-top: 50px;
  }
}

.about__description-box-mobile-margin {
  @media screen and (min-width: 768px) {
    margin-top: 0;
  }
}

.about__image {
  width: 243px;
  height: 305px;
  margin-right: 43px;

  @media screen and (min-width: 1366px) {
    width: 530px;
    height: 667px;
    margin-right: 30px;
  }
}

.about__suptitle {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 800px;
  padding-bottom: 10px;
  border-bottom: 1px solid #b9b9b9;
  color: var(--primary-text-color);

  @media screen and (max-width: 765px) {
    margin-top: 20px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 30px;
    text-align: start;
    padding-bottom: 15px;
  }
}

.about__list {
  // width: 253px;
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    // width: 375px;
    height: 165px;
  }

  @media screen and (min-width: 1366px) {
    margin-top: 30px;
    // width: 530px;
  }
}

