.about__item {
  display: flex;
  align-items: center;
}

.about__item:not(:first-child) {
  @media screen and (max-width: 1365px) {
    margin-top: 15px;
  }

  @media screen and (min-width: 1366px) {
    margin-top: 20px;
  }
}

.about__list-text {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  width: 253px;
  letter-spacing: 0.06em;
  margin-left: 9px;
  color: var(--primary-text-color);

  @media screen and (min-width: 768px) {
    width: 375px;
    font-size: 12px;
    line-height: 1.25;
  }

  @media screen and (min-width: 1366px) {
    font-size: 16px;
    width: 530px;
    margin-left: 35px;
  }
}

.about__span {
  display: flex;
  align-items: center;
  height: 33px;
  margin-top: 19px;
  margin-bottom: 5px;
  color: var(--all-orange-color);

  @media screen and (min-width: 768px) {
    margin-top: 0;
  }

  @media screen and (min-width: 1366px) {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}

.about__paragraph {
  font-size: 12px;
  line-height: 1.66;
  font-weight: 500;
  width: 279px;
  color: var(--header-menu-text-color);

  &:not(:first-child) {
    @media screen and (min-width: 1366px) {
      margin-top: 15px;
    }
  }

  @media screen and (min-width: 768px) {
    width: 401px;
    font-size: 10px;
    line-height: 2;
  }

  @media screen and (min-width: 1366px) {
    width: 641px;
    font-size: 18px;
    line-height: 1.56;
  }

  &:last-child {
    margin-bottom: 50px;
  }
}

.about__link {
  display: block;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
  margin-top: 8px;
  color: var(--all-orange-color);

  @media screen and (min-width: 768px) {
    margin-top: 24px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 18px;
    line-height: 1.56;
    margin-top: 50px;
  }
}

.about__item::before {
  content: ' ';
  background: url(../images/svg/icon-tick.svg);
  background-repeat: no-repeat;
  display: block;
  height: 20px;
  width: 24px;

  @media screen and (min-width: 1366px) {
    height: 20px;
    width: 24px;
  }

